.Deck {
    text-align: center;
}

.Deck--NeedsShuffle {
    background-color: rgb(230, 88, 22);
}

.Deck--Button {
    padding: 12px;
    background-color: inherit;
}

.Deck--Button--NeedsShuffle {
    -webkit-animation: pulse 1.2s infinite;
            animation: pulse 1.2s infinite;
}

@-webkit-keyframes pulse {
    0%, 100% {
      background-color: rgb(28, 119, 255);
    }
    30%, 40% {
      background-color: rgb(30, 255, 255);
    }
 }

@keyframes pulse {
    0%, 100% {
      background-color: rgb(28, 119, 255);
    }
    30%, 40% {
      background-color: rgb(30, 255, 255);
    }
 }

.Deck--CardBack {
    width: 200px;
    margin: 12px;
}

.Deck--PlayedCards {
    margin-top: 12px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}


